<template>
  <div class="collects">
    <nav-bar class="nav-barl">
      <div slot="left" class="nav-left" @click="backClick">
      <img src="~assets/img/back.svg" alt="">
    </div>
    <div slot="center" class="nav-titles">我的收藏（{{getGoodsLength}}）</div>
    </nav-bar>
    <scroll class="content" ref="scroll" :probe-type="3" @pullingUp="loadMore" :pull-up-load="true"> 
      <!--展示收藏的商品-->
      <goods-list :goods="showGoods"></goods-list>
    </scroll>
  </div>
</template>

<script>
  import NavBar from 'common/navbar/NavBar'
  import GoodsList from 'content/goods/GoodsList'
  import Scroll from 'common/scroll/Scroll'
  import {getAllCollectById} from 'network/collect'
  export default {
    name: 'Collect',
    components: {
      NavBar,
      GoodsList,
      Scroll
    },
    data () {
      return {
        //保存收藏商品的信息
        showGoods:[]  
      }
    },
    computed: {
      //获取用户信息
      getUserInfo() {
        return this.$store.state.userInfo
      },
      //获取收藏数量
      getGoodsLength() {
        return this.showGoods.length
      }
    },
    activated () {
      //将数组清空
      this.showGoods = [];
      //请求收藏商品信息
      getAllCollectById(this.getUserInfo.uid).then(res => {
        for(let item of res) {
          let pro = {};
          pro.gid = item.gid;
          pro.gsize6 = item.cgprice;
          pro.uid = item.uid;
          pro.gcollect = item.cgcollect;
          pro.gimage = item.cgimg;
          pro.gtitle = item.cgtitle;
          pro.cid = item.cid;
          this.showGoods.push(pro);
        }
      })
    },
    methods: {
      //返回
      backClick() {
        this.$router.back()
      },
      loadMore() {

      } 
    }
  }
</script>

<style scoped>
  .collects{
    position: relative;
    height: 100vh;
  }
   .nav-barl{
    background-color: #ff8198;
    color: white;
  }
  .nav-left{
    margin-top: 5px;
  }
  .content{
    /*使用定位获取better-scroll的高度 */
    position: absolute;
    top: 44px;
    left: 0;
    right: 0;
    bottom: 45px;
    overflow: hidden;
  }
</style>